.section__title--main {
  margin-top: 15px;
  margin-bottom: 15px;
  .section-title__circle {
    border: 2px solid $orange1;
    border-radius: 50%;
    display: inline-block;
    text-align: center;
    padding: 6px;
    width: 50px;
    height: 50px;
    & :first-child {
      text-align: center;
      font-size: 23px;
      color: $gray2;
    }
  }
  .section-title__text {
    color: $gray2;
    padding-inline-start: 10px;
  }
}
